@import url(https://fonts.googleapis.com/css?family=Open+Sans:800|Roboto+Mono:300i&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    background-color:rgb(9, 11, 12)!important;
    text-align: center;
}
  
  .App-logo {
    height: 40vmin;
  }
  
  .Title{
    color:#ffd700!important;
    text-transform: capitalize;
    font-family: 'Open Sans', sans-serif;
    font-weight: 800;
    text-align:center;
    margin-bottom:0!important;
  }
  
  .Credit {
    color : rgb(86, 120, 182);
    font-size: x-small!important;
  font-family: 'Roboto Mono', monospace;
  }

  .App-header {
    background-color:rgb(9, 11, 12)!important;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
  }
  
  .App-link {
    color: #09d3ac;
  }
  
.card{
    background:rgb(14, 16, 17)!important;
}

.table{
    background-color:none!important;
    background:none!important;
}

th{
    color:#FFAB00!important;
}

.hide{
    display:none;
}
.bt-active > .button{
    background: #dbbc09!important;
    color:white!important;
}
